import React from "react";
import Typed from "react-typed";
import {
	LinkedinShareButton,
	LinkedinIcon,
    EmailShareButton,
    EmailIcon

} from "react-share"

const Header = () => {
    return (
        <div id="home"className="header-wrapper">
            <div className="main-info">
                <h1>Alejandro Perez</h1>
                <Typed 
                className="typed-text"
                strings={["Web Design" , "Web Development" , "JavaScript" , "Bootstrap"]}
                typeSpeed={40}
                backSpeed={60}
                loop
                />
                <div className="d-flex justify-content-center">
                                                <LinkedinShareButton
                                                    url={"https://www.linkedin.com/"}
                                                    quote={"FullStack Developer"}
                                                    hashtag="#JavaScript"
                                                >
                                                    <LinkedinIcon className="mx-3" size={36} />
                                                </LinkedinShareButton>
                                                <EmailShareButton
                                                    url={"https://www.gmail.com/"}
                                                >
                                                    <EmailIcon className="mx-3" size={36} />
                                                </EmailShareButton>
                                                <a className="btn-main-offer" href="Alejandro Perez Resume.pdf" download="resume.pdf">CV</a> 
                                             </div>
               {/* <a className="btn-main-offer" href="Alejandro Perez Resume.pdf" download="resume.pdf">Download Resume</a> */}
            </div>
        </div>
    )
}

export default Header
